import { Dialog, DialogContent, IconButton } from '@mui/material';
import { useEffect, useState } from 'react';
import CloseIcon from '@mui/icons-material/Close';
import React from 'react';
import { autoJsStoreUtils, autoWebViewJs } from '@droid/android';

interface PrivacyDialogDisplayProps {
  open: boolean;
  onClose: () => void;
  type: string;
  area: string;
}

interface IPrivacyInfo {
  agree: boolean;
  disagreeTime: number;
}

/**
 * 纯显示组件 - 负责隐私协议弹窗的显示和交互
 * 可以被手动控制打开/关闭
 */
export function PrivacyDialogDisplay({ open, onClose, type, area }: PrivacyDialogDisplayProps) {
  let suffix = (typeof window === "undefined") ? "" : window.location.href.indexOf("qiniu") > -1 ? ".html" : "/"
  const [iframeSrc, setIframeSrc] = useState(`/privacies/${type}/${area}${suffix}`);
  const iframeRef = React.useRef<HTMLIFrameElement>(null);

  useEffect(() => {
    setIframeSrc(`/privacies/${type}/${area}${suffix}`);
  }, [type, area]);

  useEffect(() => {
    const handleMessage = (event: MessageEvent) => {
      // 安全检查：确保消息来自iframe
      if (event.source !== iframeRef.current?.contentWindow) {
        return;
      }

      const { sender, agree } = event.data;

      if (sender === 'privacy_page') {
        console.log(`Received privacy decision: ${agree ? 'Agreed' : 'Disagreed'}`);

        const value: IPrivacyInfo = {
          agree,
          disagreeTime: agree ? 0 : Date.now()
        };

        // 保存到本地存储
        autoJsStoreUtils.write(`privacy_${type}`, value);
        
        // 同步到Android端
        const script = `
          var data = new com.fanfanlo.emergencycall.data.PrivacyData(${value.agree}, ${value.disagreeTime});
          com.fanfanlo.emergencycall.data.PrivacyStateHolder.updateMainPrivacyByJs(data);
        `;
        autoWebViewJs.callScript(script);

        // 关闭弹窗
        onClose();
      }
    };

    window.addEventListener('message', handleMessage);

    return () => {
      window.removeEventListener('message', handleMessage);
    };
  }, [type, onClose]);

  return (
    <Dialog
      open={open}
      onClose={onClose}
      PaperProps={{
        sx: {
          width: '80%',
          height: '80%',
          maxWidth: 'none',
        },
      }}
    >
      <IconButton
        aria-label="close"
        onClick={onClose}
        sx={{
          position: 'absolute',
          right: 8,
          top: 8,
          color: theme => theme.palette.grey[500],
        }}
      >
        <CloseIcon />
      </IconButton>
      <DialogContent sx={{ padding: 0, overflow: 'hidden' }}>
        {iframeSrc && (
          <iframe
            ref={iframeRef}
            src={iframeSrc}
            style={{ width: '100%', height: '100%', border: 'none' }}
            title={`Privacy Policy - ${type}/${area}`}
          />
        )}
      </DialogContent>
    </Dialog>
  );
}