import { useEffect, useState } from "react";
import { PrivacyDialogAuto } from "./PrivacyDialogAuto";
import { autoJsStoreUtils } from "@droid/android";


export function ListenShowPrivacyContent({type}:{type:string}){

    const [open, setOpen] = useState(false);
    function onClose() {
        setOpen(false);
    }

    useEffect(()=>{
        const value = autoJsStoreUtils.read(`privacy_${type}`, false)
        console.log('privacy value is', value, typeof value);
    }, [])
    return(
        <PrivacyDialogAuto type={type} area="zh-cn" />
    )
}